 .data-list__wrap {
    position: relative;
    display: inline-block;
}
.data-list__wrap .data-list__options {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 999;
    min-width: 100%;
    max-height: 500px;
    overflow-y: auto;
    border: 1px solid #ccc;
    background-color: #fff;
    margin: 0;
    padding: 0;
}
.data-list__wrap .data-list__options li {
    list-style: none;
    margin: 0;
    padding: 8px 15px;
    cursor: pointer;
}
.data-list__wrap .data-list__options li:hover {
    background-color: #ccc;
}
.data-list__item {
    display: inline-block;
    background-color: #ccc;
    padding: 5px 10px;
    margin: 0 8px 8px 0;
    border-radius: 15px;
}
.data-list__item .close {
    cursor: pointer;
}
